@font-face {
  font-family: "SFUIText";
  font-weight: 400;
  font-style: normal;
  src: local("SFUIText"),
    url("../fonts/SFUIText-Regular.woff2") format("woff2"),
    url("../fonts/SFUIText-Regular.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  font-family: "SFUITex", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: @grey;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  padding: 0 125px;
  width: 1360px;
  margin: 0 auto;
}

.page-footer__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-footer {
  background-color: rgba(118, 129, 140, 0.2);
  padding: 40px 0;
  border-top: 5px solid @main;
}

.contacts-list__link {
  color: @grey;

  &:hover {
    color: @main;
  }
}

.contacts-list__item {
  margin: 5px 0;
}
