
.works-part {
  display: flex;
  margin: 20px 0;
}

.works-title {
  font-size: 30px;
  text-align: center;
  padding: 10px 10px;
  font-weight: 400;
  letter-spacing: 3px;
  text-transform: uppercase;
}

.works-link {
  width: 1200px;
}

.works-img {
  &:hover {
    transform: scale(0.95);
  }
}

.works-button {
  color: @main;
  border: dotted 1px @main;
  padding: 15px 15px;
  border-radius: 10px;
  font-size: 18px;

  &:hover {
    color: #ffffff;
    background-color: @main;
    border: none;
  }
}

.works-name {
  font-size: 18px;
  margin: 0;
  color: @main;
}

.works-about {
  flex: 0 0 500px;
  margin-left: 60px;
}

.works-description {
  margin-bottom: 50px;
  padding: 0;
  font-size: 16px;
}
